
import { defineComponent } from "vue";

export default defineComponent({
  name: "AtList",
  emits: ["update:curIndex", "click-item"],
  props: {
    showSubjectTitle: {
      type: Boolean,
      default: true,
    },
    list: {
      type: Array,
      required: true,
    },
    curIndex: {
      type: Number,
      required: true,
    },
  },
  setup(props, ctx) {
    return {
      onMouseEnter(index: number) {
        ctx.emit("update:curIndex", index);
      },
    };
  },
});
